body{margin:0;padding:0;font-size:16px;box-sizing:border-box;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}li,ul,p,h1,h2,h3,h4,h5,a{font-family:"Antic Slab";margin:0;padding:0;color:#282828}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}.fade-in-enter{opacity:0;transform:translateY(20px)}.fade-in-enter-active{opacity:1;transform:translateY(0)}.fade-in-exit{opacity:1;transform:translateY(0)}.fade-in-exit-active{opacity:0;transform:translateY(20px)}.fade-in-appear{opacity:0;transform:transateY(20px)}.fade-in-appear-done{opacity:1;transform:transateY(0)}.fade-in-exit-active,.fade-in-enter-active,.fade-in-appear-done{transition:opacity 300ms ease-in,transform 300ms ease}.slide-in-enter{opacity:0;transform:translate(50%, 50%)}.slide-in-enter-active{opacity:1;transform:translate(-50%, -50%)}.slide-in-exit{opacity:1;transform:translate(-50%, -50%)}.slide-in-exit-active{opacity:0;transform:translate(50%, 50%)}.slide-in-exit-active,.slide-in-enter-active{transition:opacity 300ms ease,transform 300ms ease}.popup-enter{opacity:0}.popup-enter-active{opacity:1;transition:opacity 300ms ease-in-out}.popup-exit{opacity:1}.popup-exit-active{opacity:0;transition:opacity 300ms ease-in-out}

.location-selector{background-color:#cc241d;height:100vh;width:100vw;display:flex;justify-content:center;align-items:center;flex-flow:row nowrap}.location-selection-container{position:relative;height:100%;flex:1 1;background-color:#458588;display:flex;justify-content:center;align-items:center;transition:all 300ms ease-in-out}.location-selection-container:nth-of-type(odd){background-color:#b16286}.location-selection-container:nth-of-type(even){background-color:#458588}.location-selection-container:hover{transition:all 300ms ease-in-out;cursor:pointer;flex-grow:1.1}.location-selection{pointer-events:none;position:absolute;height:100%;width:100%;background-size:cover;background-position:bottom;transition:transform ease-in-out 300ms;transform:scale(1)}.location-selection:hover{transform:scale(1.1);transition:transform ease-in-out 300ms}.location-selection-overlay{pointer-events:auto;background-color:rgba(10,10,10,0.5);height:100%;width:100%;position:absolute relative;top:0;left:0;z-index:100;transition:background-color 300ms ease-in-out;display:flex;justify-content:center;align-items:center;flex-direction:column}.location-selection-overlay:hover{background-color:rgba(0,0,0,0);transition:background-color 300ms ease-in-out}.location-selection-overlay:hover .hide{opacity:1;transition:opacity 300ms ease-in-out}.location-selection-overlay:hover h1,.location-selection-overlay:hover a{color:#282828;background-color:#ebdbb2;padding:1rem;border-radius:25px}.location-selection-overlay h1,.location-selection-overlay a{color:#ebdbb2;text-transform:uppercase}.hide{opacity:0;transtion:opacity 300ms ease-in-out}@media (max-width: 700px){.location-selector{flex-direction:column;align-items:stretch}}

.popup-content{text-align:justify;color:#282828;display:flex;flex-direction:column;justify-content:center;align-items:center}.popup-content h2{text-align:center;padding:10px;border-bottom:#282828 solid 0.2rem}.popup-content button{color:#ebdbb2;background:#282828;border:none;outline:none;height:2.5em;font-family:"Antic Slab";font-weight:700}.popup-content button:hover{cursor:pointer}

.popup-menu{background-color:#ebdbb2;position:absolute;width:300px;padding:15px;box-shadow:rgba(0,0,0,0.25) 0px 54px 55px,rgba(0,0,0,0.12) 0px -12px 30px,rgba(0,0,0,0.12) 0px 4px 6px,rgba(0,0,0,0.17) 0px 12px 13px,rgba(0,0,0,0.09) 0px -3px 5px;transform:translateX(-50%);font-size:0.9rem}.popup-menu:after{content:" ";position:absolute;right:50%;top:-15px;transform:translateX(50%);border-top:none;border-right:15px solid transparent;border-left:15px solid transparent;border-bottom:15px solid #ebdbb2}

.spinner-container{top:50;left:50;height:100vh;width:100%;display:flex;justify-content:center;align-items:center;background-color:#ebdbb2;display:flex;justify-content:center;align-items:center;flex-direction:column}.spinner-container .spinner{margin:4rem}.spinner-container h1{margin:2rem}

.details-header{width:50%;margin-bottom:30px;display:flex;justify-content:center;align-items:center}.details-header h1{color:#282828;font-size:3rem;margin-bottom:10px;border-top:#282828 solid 0.4rem;border-bottom:#282828 solid 0.4rem;padding:5px}.details-icon-container{display:flex;justify-content:center;align-items:center;background-color:white;border-radius:50%;height:13rem;width:13rem;margin-right:30px}.details-icon-container img{max-height:230px;max-width:230px;width:auto;height:auto}.details-icon-image{height:200px}

.details-content-container{height:100vh;width:80%;display:flex;flex-direction:column;justify-content:flex-start;align-items:center}.details-content-title{width:100%;margin:1rem 0 2rem 0;padding-bottom:1rem;border-bottom:4px solid #282828;text-align:center}.details-content-items{list-style:none;padding:20px;display:flex;flex-wrap:wrap;align-items:center}.details-content-item{flex-basis:50%;height:3rem}.details-item-container{font-size:1.1rem;font-weight:bold;display:flex;margin:0 auto;align-items:center}.details-item-container span{font-size:2rem;display:flex;justify-content:center;align-items:center;margin-right:10px}.details-item-container a:hover{cursor:pointer}

.gallery-container{height:94vh;width:100%;display:flex;justify-content:center;align-items:center;flex-direction:column;background:#458588}.photo-grid-item{height:100%;width:100%;display:grid;grid-gap:10px;grid-template-columns:repeat(5, 1fr);grid-template-rows:repeat(6, 1fr)}.photo-grid-item .photo-container{font-family:"Patua One";font-size:2rem;background-color:#928374;color:#ebdbb2;overflow:hidden;box-shadow:2px 2px 10px var(--color-black)}.photo-grid-item .photo-container:nth-child(4){grid-column:span 1;grid-row:span 1}.photo-grid-item .photo-container:nth-child(1),.photo-grid-item .photo-container:nth-child(3){grid-column:span 2;grid-row:span 2}.photo-grid-item .photo-container:nth-child(5){grid-column:span 2;grid-row:span 3}.photo-grid-item .photo-container:nth-child(6){grid-column:span 3;grid-row:span 2}.photo-grid-item .photo-container:nth-child(2){grid-column:span 3;grid-row:span 3}.photo-grid-item .photo-container:hover .photo{color:#d79921}.photo{height:100%;width:100%;display:flex;align-items:center;justify-content:center;background-size:cover;background-position:center;transition:all ease-in-out 300ms}.photo:hover{transform:scale(1.1);transition:all ease-in-out 300ms}.full{display:flex;height:100vh;width:100%;justify-content:center;align-items:center}

.details-container{background-color:rgba(0,0,0,0.7);height:100vh;width:100vw;position:fixed;top:0;left:0}.details{box-sizing:border-box;height:95%;width:97%;position:fixed;top:50%;left:50%;transform:translate(-50%, -50%);background:#ebdbb2;z-index:10;padding:50px 50px 0 50px;display:flex;flex-direction:column;align-items:center;overflow:scroll;box-shadow:2px 2px 10px var(--color-black)}.details-top{position:relative;height:100%;display:flex;justify-content:center;align-items:center;flex-direction:column}.btn-close{position:fixed;background:#d79921;border:none;top:10px;right:10px;font-size:2em;outline:none;display:flex;justify-content:center;align-items:center}.btn-close:hover{cursor:pointer}

.footer-container{box-sizing:border-box;bottom:0;height:100px;width:100vw;background-color:#282828;color:#ebdbb2;display:flex;justify-content:center;align-items:flex-start}.footer-container h4,.footer-container a{color:#b8bb26;font-family:"Noto Sans";margin:0.2rem 0}.footer-info{display:flex;justify-content:center;align-items:flex-start;flex-direction:column;margin-left:6rem;flex:1 1;padding:10px}.footer-icon{flex:1 1;padding:10px;border:none;font-size:3rem;margin-right:6rem;display:flex;justify-content:flex-end;align-items:center}.footer-icon:hover{cursor:pointer}@media (max-width: 700px){.footer-container{flex-direction:column;justify-content:center;align-items:center;height:10rem}.footer-info{align-items:center;margin:0}.footer-icon{justify-content:center;margin:0}}

.mainmap{position:relative;background:#282828}.mainmap-image{width:100vw;height:auto}.mainmap-icons{list-style:none}.mainmap-icon{position:absolute;transform:scale(1) translate(-50%, -50%);transform-origin:top left;transition:transform 300ms ease}.mainmap-icon:hover{cursor:pointer}.mainmap-icon-active{transform:scale(1.1) translate(-50%, -50%)}.mainmap-icon:hover{transform:scale(1.1) translate(-50%, -50%);transform-origin:top left;transition:transform 300ms ease}.mainmap-icon-image{max-height:12vw}

